.App {
  text-align: center;
}


body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  width: 60%;
  margin: auto;
  background: rgb(248, 246, 237);
  margin-bottom: 50px;
}

h1 {
  text-align: center;
}

.loading {
  text-align: center;
  font-size: 200%;
  height: 200px;
  line-height: 200px;
}

.user p {
  word-break: break-word;
}
.numbers {
  text-align: center;
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

a {
  text-decoration: none;
  font-size: 20px;
  padding: 5px 10px;
  border: 1px solid #ccc;
  background: rgb(44, 51, 134);
  color: #fff;
  margin: 5px;
}

.active {
  background-color: #7f7373;
}
/* NAVBAR*/
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  background-color: rgb(3, 17, 73);
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid rgb(254, 215, 215);
  border-radius: 0 0 30px 30px;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar a:hover {
  color: rgb(255, 160, 160);
}

.btn-navbar {
    width: 100%;
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 60px;
}

.links {
  width: 70%;
  height: 80px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
}

.links a {
  width: 150px;
  height: 50px;
  background-color: rgb(9, 6, 99);
  border-radius: 5px;

  align-items: center;
  justify-content: center;
}

nav {
  margin: 0;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  text-decoration: none;
}

.enable{
  color: rgb(32, 113, 32);
  cursor: pointer;
}

.disable{
  color: red;
  cursor: pointer;
}

.error button{
  background-color: rgb(17, 16, 16);
  color: #f44336;
  padding: 15px 32px;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

#confetti button {
  background-color: #404663;
  color: #fff;
  border: 0;
  cursor: pointer;
  font-size: 2rem;
  font-weight: 400;
  padding: 0.5em 1.25em;
  border-radius: 0.5em;
  z-index: 999;
  position: relative;
  display: flex;
  gap: 0.5em;
  box-shadow:
    0px 1.7px 2.2px rgba(0, 0, 0, 0.02),
    0px 4px 5.3px rgba(0, 0, 0, 0.028),
    0px 7.5px 10px rgba(0, 0, 0, 0.035),
    0px 13.4px 17.9px rgba(0, 0, 0, 0.042),
    0px 25.1px 33.4px rgba(0, 0, 0, 0.05),
    0px 60px 80px rgba(0, 0, 0, 0.07);
}

